import React from 'react'
import NewVacationForm from "./newVacationForm";
import { useSelector } from 'react-redux'
import { LOGGED_IN, LOGGED_OUT, ADMIN } from '../../redux/appStateTypes';
import { Redirect } from 'react-router-dom';
import TripAdmin from "./TripAdmin";
import AdminChart from './AdminChart';
import LoadingSpinner from '../../assets/LoadingSpinner';
import "./admin.scss";

/* this is a functional reat component
    that handles the logic and display of admin features
*/
const Admin = () => {
    
    // get state variables
    const appstate = useSelector(state => state.user.appState);
    const trips = useSelector(state => state.trips);

    // this helps protect the admin view from unauthorized visits
    let route;
    // same as we use in all top level components - if authorized null, else
    // redirect
    switch (appstate) {
        case LOGGED_IN:
            route = <Redirect exact to ="/trips"/>;
            break;
        case LOGGED_OUT:
            route = <Redirect exact to = "/home"/>;
            break;
        case ADMIN:
            route = null;
            break;
        default:
            break;
    }

    return (
        <div className = "main">
            {route}
            {trips.error?<div>there was an error</div>:null}
            { trips.isLoading? <LoadingSpinner/>: null }
                <div className = "grid">
                    <div className = "gridHeader">ID</div>
                    <div className = "gridHeader">Destination</div>
                    <div className = "gridHeader">description</div>
                    <div className = "gridHeader">price</div>
                    <div className = "gridHeader">start date</div>
                    <div className = "gridHeader">end date</div>
                    <div className = "gridHeader">pic</div>
                    <div className = "gridHeader">actions</div>
                    <NewVacationForm/>  
                    { !trips.isLoading && trips.trips && trips.trips.map(trip=><TripAdmin tripinfo = {trip} key = {trip.vacation_id}/>)}
                </div>
            <div className = "showReport"><i className="material-icons">show_chart</i></div>
            <AdminChart/>
	    </div>
    )
}

export default Admin;
