import React from "react";
import { useSelector } from "react-redux";
import Logo from "../../assets/Logo";
import LoadingSpinner from '../../assets/LoadingSpinner';
import { LOGGED_IN, ADMIN } from  "../../redux/appStateTypes";
import LoginForm from "./LoginForm";
import LogoutButton from "./LogoutButton";
import "./header.scss";

// this is a react functional component that houses the header
function Header() {
  // get state
  const user = useSelector(state => state.user);
  const logState = useSelector(state => state.user.appState);
  const loggedin = ((logState === LOGGED_IN)||(logState === ADMIN));
  // if the user is logged in, it will show the logout and a welcome message

  return (
    <div className="header">
      <div className="left">
        <Logo/>
        <h2>MY WAY</h2>
      </div>
      {(user.loading) ? <LoadingSpinner/>:
      <div className="right">
        {loggedin? <span> hello {user.fname} {user.lname} </span>:null}
        {loggedin? <LogoutButton/>:<LoginForm/>}
      </div>}
    </div>
  );
}

export default Header
