import React, { useState } from "react";
import { Redirect } from "react-router-dom"
import { registerUser } from "../redux/userActions";
import { useSelector, useDispatch } from "react-redux";
import { LOGGED_IN, LOGGED_OUT, ADMIN } from "../redux/appStateTypes"
import "./register.scss";

// this is a functional react component that handles the registry of new 
// users
const Register = () => {
  const appstate = useSelector(state => state.user.appState);
  const [body, setBody] = useState({
    uname: "",
    fname: "",
    lname: "",
    pword: ""
  });
  const message = JSON.stringify(useSelector(state => state.user.error));
  const dispatch = useDispatch();
  let route = {}; 

  // this handles rerouting in case app state is wrong for accessing this component
  switch (appstate) {
    case LOGGED_IN:
      route = <Redirect exact to="/trips" />;
      break;
    case LOGGED_OUT:
      route = null;
      break;
    case ADMIN:
      route = <Redirect exact to="/admin" />;
      break;
    default:
      break;
  }

  const submitHandler = e => {
    e.preventDefault();
    dispatch(registerUser(body));
  };

  const changeHandler = e => {
    setBody({
      ...body,
      [e.target.name]: e.target.value
    });
  };
  return (
    <div className="registerForm main">
    {route}
      <form onSubmit={submitHandler}>
        <div>
          <label htmlFor="uname"> User name: </label>
          <input
            type="text"
            required
            autoComplete="off"
            name="uname"
            value={body.uname}
            onChange={changeHandler}
          />
        </div>
        <div>
          <label htmlFor="fname">first name: </label>
          <input
            type="text"
            required
            autoComplete="off"
            name="fname"
            value={body.fname}
            onChange={changeHandler}
          />
        </div>
        <div>
          <label htmlFor="lname">last name: </label>
          <input
            type="text"
            required
            autoComplete="off"
            name="lname"
            value={body.lname}
            onChange={changeHandler}
          />
        </div>
        <div>
          <label htmlFor="pword">password: </label>
          <input
            type="password"
            required
            minLength="6  "
            autoComplete="off"
            name="pword"
            value={body.pword}
            onChange={changeHandler}
          />
        </div>
        <div>
          <button className="login" type="submit">
            Sign Up!
          </button>
          {message ? <span className="message">{message}</span> : null}
        </div>
      </form>
    </div>
  );
};

export default Register;
