import React from "react";
import { useSelector } from "react-redux";
import { LOGGED_IN, LOGGED_OUT, ADMIN } from "../redux/appStateTypes";
import { Redirect, Link } from "react-router-dom";
import "./home.scss";

// this is a functional react component that handles the home page display
const Home = () => {
  //initialize variables
  const appstate = useSelector(state => state.user.appState);
  let route;

  // this handles rerouting in case app state is wrong for accessing this component
  switch (appstate) {
    case LOGGED_IN:
      route = <Redirect exact to="/trips" />;
      break;
    case LOGGED_OUT:
      route = null;
      break;
    case ADMIN:
      route = <Redirect exact to="/admin" />;
      break;
    default:
      break;
  }

  return (
    <div className="main">
      {route}
      <div className="home-wrapper">
        <main>
          <p>
            <b>MY WAY</b> Gets You On Vacation, Now.
          </p>
          Join Us and see!<p></p>
        </main>
        <Link className="link" to="/register">
          Join Now!
        </Link>
      </div>
    </div>
  );
};

export default Home;
