import { UPDATE_TRIPS_BEGIN, UPDATE_TRIPS_SUCCESS, UPDATE_TRIPS_FAILURE, LOGOUT } from "../actionTypes";

const initialState = {
  trips: [],
  loading:false, 
  error:""
};

export default function(state = initialState, action) {
    switch (action.type) {
        case UPDATE_TRIPS_BEGIN: {
            return {
            ...state,
            loading:true,
            error:null
            }
        }
        case UPDATE_TRIPS_SUCCESS: {
            return {
                ...state,
                loading:false,
                trips: action.payload,
            }
        }
        case UPDATE_TRIPS_FAILURE:{
            console.log(action);
            
            return {
                ...state,
                loading:false,
                error:action.payload,
                trips:[]
            }
        }
        case LOGOUT:{
            return{
                ...state,
                trips:[]
            }
        }
    default:
      return state;
  }
}