import React, { useEffect } from "react";
import "./App.scss";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import Header from "./components/HeaderComponents/Header";
import Footer from "./components/Footer";
import Home from "./components/Home";
import Admin from "./components/AdminComponents/Admin";
import Trips from "./components/TripsComponent/Trips";
import Page404 from "./components/Page404";
import Register from "./components/Register";

import bgImg from "../src/assets/anotherbeach.jpg";
import { useSelector, useDispatch } from "react-redux";
import { LOGGED_OUT } from "./redux/appStateTypes";
import { getTrips } from "./redux/tripActions";
import AdminChart from "./components/AdminComponents/AdminChart";
import { useInterval } from "./useInterval";

function MyWayApp() {
  const user_id = useSelector(state => state.user.user_id);
  const appstate = useSelector(state => state.user.appState);
  const dispatch = useDispatch();
  // on load, we fetch all trips for the user (sorted by the server so that followed should be first)
  useEffect(() => {
    if (appstate !== LOGGED_OUT)
      dispatch(getTrips(user_id, appstate === AdminChart));
  }, [appstate, user_id, dispatch]);
  // refreshes the access token
  const refreshLoop = async () => {
    const refreshToken = await asyncLocalStorageGet("refreshToken");
    if (refreshToken) {
      fetch("http://localhost:3000/auth/token", {
        method: "POST",
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify({ token: refreshToken })
      })
        .then(res => res.json())
        .then(json => {
          localStorage.setItem("accessToken", json.accessToken);
        });
    }
  };
  // refreshes the trips, replaces web socket
  const refreshTrips = async () => {
    const refreshToken = await asyncLocalStorageGet("refreshToken");
    if (refreshToken) dispatch(getTrips);
  };
  useInterval(refreshLoop, 1000 * 60 * 11);
  // enable this to get web socket functionality
  // useInterval(refreshTrips, 1000 * 60 *10);
  useEffect(() => refreshLoop());
  return (
    <div className="App">
      <img className="background" src={bgImg} alt="beach" />
      <Header />
      <BrowserRouter>
        <Switch>
          <Route path="/home">
            <Home />
          </Route>
          <Route path="/trips">
            <Trips />
          </Route>
          <Route path="/admin">
            <Admin />
          </Route>
          <Route path="/register">
            <Register />
          </Route>
          <Route exact path="/">
            <Home />
          </Route>
          <Route path="*">
            <Page404 />
          </Route>
        </Switch>
      </BrowserRouter>
      <Footer />
    </div>
  );
}

export default MyWayApp;

const asyncLocalStorageGet = async key => {
  return Promise.resolve().then(() => localStorage.getItem(key));
};

