import React, { useState } from "react";

/*
    this is a functional react component that handles diplaying a single vacation for
    admin, as well as handling the delete and update logic.
*/
const TripAdmin = trip => {
  // initialize data from the prop
  const [formData, setFormData] = useState({
    vacation_id: trip.tripinfo.vacation_id,
    destination: trip.tripinfo.destination,
    description: trip.tripinfo.description,
    sdate: trip.tripinfo.sdate,
    edate: trip.tripinfo.edate,
    pic: trip.tripinfo.pic,
    price_usd: trip.tripinfo.price_usd
  });

  // handles the update request
  const submitHandler = async e => {
    e.preventDefault();
    const aToken = await asyncLocalStorageGet("accessToken");
    fetch("http://localhost:3000/vacations", {
      method: "PUT",
      headers: {
        Authorization: "Bearer " + aToken,
        "Content-Type": "application/json"
      },
      body: JSON.stringify(formData)
    })
      .then(response => {
        // we want the response even on error because it contains message
        let json = response.json();
        if (
          (response.status >= 200 && response.status < 300) ||
          (response.status >= 400 && response.status < 410)
        ) {
          return json;
        } else {
          return json.then(Promise.reject.bind(Promise));
        }
      })
      .then(data => {
        if (data.success) {
          setFormData({});
        }
      })
      .catch(error => {
        console.log(error);
      });
  };

  // deletes an item from the db
  const deleteHandler = async e => {
    e.preventDefault();
    const aToken = await asyncLocalStorageGet("accessToken");
    fetch("http://localhost:3000/vacations", {
      method: "DELETE",
      headers: {
        Authorization: "Bearer " + aToken,
        "Content-Type": "application/json"
      },
      body: JSON.stringify(formData)
    })
      .then(response => {
        return response.json();
      })
      .then(data => {
        if (data.success) {
        }
      })
      .catch(error => {
        console.log(error);
      });
  };

  // handles any change in the input fields
  const changeHandler = e => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
  };

  return (
    <form className="vacationForm" onSubmit={submitHandler}>
      <div>{trip.tripinfo.vacation_id}</div>
      <input
        type="text"
        required
        autoComplete="off"
        name="destination"
        value={formData.destination}
        onChange={changeHandler}
      />
      <textarea
        required
        autoComplete="off"
        name="description"
        value={formData.description}
        onChange={changeHandler}
      />
      <input
        type="number"
        required
        autoComplete="off"
        name="price_usd"
        value={formData.price_usd}
        onChange={changeHandler}
      />
      <input
        type="date"
        required
        autoComplete="off"
        name="sdate"
        value={dateParser(formData.sdate)}
        onChange={changeHandler}
      />
      <input
        type="date"
        required
        autoComplete="off"
        name="edate"
        value={dateParser(formData.edate)}
        onChange={changeHandler}
      />
      <input
        type="text"
        autoComplete="off"
        name="pic"
        value={formData.pic}
        onChange={changeHandler}
      />
      <div>
        <button className="editVacation" type="submit">
          <i className="material-icons">edit</i>
        </button>
        <button className="editVacation" onClick={deleteHandler}>
          <i className="material-icons">delete</i>
        </button>
      </div>
    </form>
  );
};
export default TripAdmin;

// makes async get calls to localStorage by key
const asyncLocalStorageGet = async key => {
  return Promise.resolve().then(() => localStorage.getItem(key));
};

function dateParser(date) {
  let temp;
  if (date) temp = date.split("/");
  if (!temp || temp.length !== 3) return date;
  let parsedDate = `${temp[2]}-${temp[1]}-${temp[0]}`;
  return parsedDate;
}
