import { LOGIN_BEGIN, LOGIN_SUCCESS, LOGIN_FAILURE, LOGOUT } from "../actionTypes";
import { LOGGED_OUT, ADMIN, LOGGED_IN } from "../appStateTypes";

const initialState = {
  user: {},
  loading:false,
  error:"",
  appState: LOGGED_OUT,
};

export default function(state = initialState, action) {
    switch (action.type) {
        case LOGIN_BEGIN: {
            return {
                ...state,
                loading:true,
                appState:LOGGED_OUT
            }
        }
        case LOGIN_SUCCESS: {
            return {
                ...state,
                loading:false,
                user:action.payload,
                appState:action.payload.isadmin? ADMIN : LOGGED_IN
            }
        }
        case LOGIN_FAILURE: {
            return {
                ...state,
                loading:false,
                error:action.payload,
                user:{},
                appState: LOGGED_OUT
            }
        }
        case LOGOUT: {
            return {
                ...state,
                user:{},
                appState:LOGGED_OUT,
            }
        }
    default:
      return state;
  }
}