import {
  UPDATE_TRIPS_BEGIN,
  UPDATE_TRIPS_SUCCESS,
  UPDATE_TRIPS_FAILURE,
  LOGOUT
} from "./actionTypes";

export const updateTripsBegin = () => ({
  type: UPDATE_TRIPS_BEGIN
});

export const updateTripsSuccess = content => ({
  type: UPDATE_TRIPS_SUCCESS,
  payload: content
});

export const updateTripsFailure = error => ({
  type: UPDATE_TRIPS_FAILURE,
  payload: error
});

export const logoutTrips = () => ({
  type: LOGOUT
});

export const getTrips = (user_id, isAdmin) => {
  return dispatch => {
    dispatch(updateTripsBegin());
    return tripsFetch(user_id, isAdmin)
      .then(json => {
        if (!json.success) {
         console.log(json.message);
         
          dispatch(updateTripsFailure(json.message));
        }
        else dispatch(updateTripsSuccess(json.results));
        return json.trips;
      })
      .catch(error => {
        console.log("error");
        dispatch(updateTripsFailure(error));
      });
  };
};

const tripsFetch = async (user_id, isAdmin) => {
  const aToken = await asyncLocalStorageGet("accessToken");
  const endpoint = isAdmin ? "/adminGet" : "";
  return fetch("http://localhost:3000/vacations" + endpoint, {
    method: "GET",
    headers: {
      Authorization: "Bearer " + aToken,
      "Content-Type": "application/json"
    },
    body: JSON.stringify(user_id)
  })
    .then(res => {
      if (res.ok) return res;
      // if it isnt some weird network failure, i wanna know what it is
      // especially 400's i might need to know about
      if (
        !res.ok &&
        ((res.status >= 200 && res.status <= 300) ||
          (res.status >= 400 && res.status <= 405))
      )
        return res;
      else throw Error(res.statusText);
    })
    .then(res => {
      return res.json();
    });
};

const asyncLocalStorageGet = async key => {
  return Promise.resolve().then(() => localStorage.getItem(key));
};
