import React, { useState } from "react";

/*
    this is a functional react component that handles creating
    new vacations
*/
const NewVacationForm = () => {
  // initialize data from the prop
  const [formData, setFormData] = useState({
    vacation_id: "",
    destination: "",
    description: "",
    sdate: "",
    edate: "",
    pic: "",
    price_usd: ""
  });

  // handles the post request
  const submitHandler = async e => {
    e.preventDefault();
    const aToken = await asyncLocalStorageGet("accessToken");
    fetch("http://localhost:3000/vacations", {
      method: "POST",
      headers: {
        Authorization: "Bearer " + aToken,
        "Content-Type": "application/json"
      },
      body: JSON.stringify(formData)
    })
      .then(response => {
        // we want the response even on error because it contains message
        let json = response.json();
        if (
          (response.status >= 200 && response.status < 300) ||
          (response.status >= 400 && response.status < 410)
        ) {
          return json;
        } else {
          return json.then(Promise.reject.bind(Promise));
        }
      })
      .then(data => {
        if (data.success) {
          setFormData({
            vacation_id: "",
            destination: "",
            description: "",
            sdate: "",
            edate: "",
            pic: "",
            price_usd: ""
          });
        }
      })
      .catch(error => {
        console.log(error);
      });
  };

  // handles any change in the input fields
  const changeHandler = e => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
  };

  return (
    <form className="vacationForm" onSubmit={submitHandler}>
      <div>new:</div>
      <input
        type="text"
        required
        autoComplete="off"
        name="destination"
        value={formData.destination}
        onChange={changeHandler}
      />
      <textarea
        required
        autoComplete="off"
        name="description"
        value={formData.description}
        onChange={changeHandler}
      />
      <input
        type="number"
        required
        autoComplete="off"
        name="price_usd"
        value={formData.price_usd}
        onChange={changeHandler}
      />
      <input
        type="date"
        required
        autoComplete="off"
        name="sdate"
        value={formData.sdate}
        onChange={changeHandler}
      />
      <input
        type="date"
        required
        autoComplete="off"
        name="edate"
        value={formData.edate}
        onChange={changeHandler}
      />
      <input
        type="text"
        autoComplete="off"
        name="pic"
        value={formData.pic}
        onChange={changeHandler}
      />
      <div>
        <button className="addVacation" type="submit">
          Add!
        </button>
      </div>
    </form>
  );
};
export default NewVacationForm;

// makes async get calls to localStorage by key
const asyncLocalStorageGet = async key => {
  return Promise.resolve().then(() => localStorage.getItem(key));
};
