import {
  LOGIN_BEGIN,
  LOGIN_SUCCESS,
  LOGIN_FAILURE,
  LOGOUT
} from "./actionTypes";

export const loginBegin = () => ({
  type: LOGIN_BEGIN
});

export const loginSuccess = content => ({
  type: LOGIN_SUCCESS,
  payload: content
});

export const loginFailure = error => ({
  type: LOGIN_FAILURE,
  payload: error
});

export const logout = () => ({
  type: LOGOUT
});

export const getUser = body => {
  return dispatch => {
    dispatch(loginBegin(body));
    return userFetch(body)
      .then(json => {
        if (json.success) {
          localStorage.setItem("accessToken", json.accessToken);
          localStorage.setItem("refreshToken", json.refreshToken);
          dispatch(loginSuccess(json.user));
          return json.user;
        } else {
          dispatch(loginFailure(json.message));
          return json;
        }
      })
      .catch(error => {
        dispatch(loginFailure(error));
        return error;
      });
  };
};

const userFetch = body => {
  return fetch("http://localhost:3000/auth/login", {
    method: "POST",
    headers: {
      "Content-Type": "application/json"
    },
    body: JSON.stringify(body)
  }).then(res => {
    return res.json();
  });
};

export const registerUser = body => {
  return dispatch => {
    dispatch(loginBegin(body));
    return createUser(body)
      .then(json => {
        if (json.success) {
          localStorage.setItem("accessToken", json.accessToken);
          localStorage.setItem("refreshToken", json.refreshToken);          
          dispatch(loginSuccess(json.user));
        } else {
          dispatch(loginFailure(json.message));
        }
        return json.user;
      })
      .catch(error => dispatch(loginFailure(error)));
  };
};

const createUser = body => {
  return fetch("http://localhost:3000/auth/register", {
    method: "POST",
    headers: {
      "Content-Type": "application/json"
    },
    body: JSON.stringify(body)
  }).then(res => {
    return res.json();
  });
};
