import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getUser } from "../../redux/userActions";

// this is a functional react component that hanbdles the login logic
const LoginForm = () => {
  // initialize variables
  const [body, bodySet] = useState({uname:"", pword:""});

  const message = JSON.stringify(useSelector(state => state.user.error));

  const dispatch = useDispatch();

  // this function handles the login
  const submitHandler = e => {
    e.preventDefault();
    dispatch(getUser(body));
    bodySet({uname:"", pword:""})
  };

  const changeHandler = e => {
    bodySet({...body, [e.target.name]:e.target.value})
  };
  return (
    <div>
      <form onSubmit={submitHandler}>
        <div>
          <label htmlFor="uname"> User name: </label>
          <input
            type="text"
            required
            autoComplete="off"
            name="uname"
            value={body.uname}
            onChange={changeHandler}
          />
        </div>
        <div>
          <label htmlFor="pword">password: </label>
          <input
            type="password"
            required
            minLength="6  "
            autoComplete="off"
            name="pword"
            value={body.pword}
            onChange={changeHandler}
          />
        </div>
        <div>
          <button className="login" type="submit">
            Log-in!
          </button>
          {message ? <span className="message">{message}</span> : null}
        </div>
      </form>
    </div>
  );
};

export default LoginForm;
